.shopcategory-banner{
    display: block;
    margin: 0 auto;
    width: 82%;
}
.shopcategory-indexSort{
    display: flex;
    margin: 0px 170px;
    justify-content: space-between;
    align-items: center;
}
.shopcategory-sort{
        padding: 10px 20px;
        border-radius: 40px;
        border: 1px solid #888;
}
.shopcategory-indexSort p span{
    font-weight: 600;
}
.shopcategory-products{
    margin: 10px auto;
    display: grid;
    grid-template-columns: 2fr 2fr 2fr 2fr;
    column-gap: 20px; /* Increase the gap between columns */
    row-gap: 20px; /* Increase the gap between rows */

}
.shopcategory-loadmore{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 150px auto;
    width: 233px;
    height: 69px;
    border-radius: 75px;
    background: #ededed;
    color: #787878;
    font-size: 18px;
    font-weight: 500;
    
}
